import logo from './logo.svg';
import React, {useState} from 'react'
import './App.css';
import {Header} from './Header';

function App() {
    let isDisabled = true;
    const [sum, setSum] = useState(0);

    if(sum === 11) {
        isDisabled = false;
    }

    const onInputHandker = (evt) => {
        setSum(evt.turget.value + sum);
    }
    return (
        <>
        <header className="page-header">
            <div>
                <img src="./img/logo.png" alt="Логотип компании"/>
            </div>
            <p className="page-header__history">Чверть століття тому було покладено початок великого шляху. Сьогодні ми
                також сміливі, як і на самому початку.</p>
        </header>
        <main class="main-container">
            <p class="main-container__title">Первое суждение</p>
            <p class="main-container__qustion">Я полагаю, что мог внести большой вклад на такой работе где</p>
            <form class="qustion-form">
                <div class="qustion-form__block">
                    <label class="qustion-form__label" for="q1">Ответ №1</label>
                    <input onChange={onInputHandker} class="qustion-form__input" type="number" id="q1"/>
                        <p class="qustion-form__description">а) хорошая заработная плата и прочие виды
                            вознаграждения</p>
                </div>
                <div class="qustion-form__block">
                    <label class="qustion-form__label" for="q1">Ответ №2</label>
                    <input onChange={onInputHandker} class="qustion-form__input" type="number" id="q1"/>
                        <p class="qustion-form__description">b) имеется возможность установить хорошие взаимоотношения с
                            коллегами на работе
                        </p>
                </div>
                <div class="qustion-form__block">
                    <label class="qustion-form__label" for="q1">Ответ №3</label>
                    <input onChange={onInputHandker} class="qustion-form__input" type="number" id="q1"/>
                        <p class="qustion-form__description">c) я мог бы влиять на принятие решений и демонстрировать
                            свои
                            достоинства как работника
                        </p>
                </div>
                <div class="qustion-form__block">
                    <label class="qustion-form__label" for="q1">Ответ №4</label>
                    <input onChange={onInputHandker} class="qustion-form__input" type="number" id="q1"/>
                        <p class="qustion-form__description">d) у меня есть возможность совершенствоваться и расти как
                            личность
                        </p>
                </div>
                <button type="submit" class="qustion-form__button" disabled={isDisabled}>Наступне питання</button>
            </form>
        </main>
            </>);
}

export default App;
